<svelte:head>
	<title>Spaper | Documentation</title>
</svelte:head>

<div>
  <Badge type="secondary"><h2>Spaper</h2></Badge>
  <p>
    <strong>PaperCSS</strong> components for <strong>Svelte</strong>
  </p>
  <p><code>
    $ npm i spaper
  </code></p>
  <p>
    <Button type="secondary" href="#/start">
      Get Started
    </Button>
    <Button external href="https://github.com/Oli8/spaper/releases/">
      Changelog
    </Button>
  </p>
  <hr class="margin-bottom" />
  <a class="github-button" href="https://github.com/Oli8/spaper" data-color-scheme="no-preference: dark; light: light; dark: dark;" data-size="large" data-show-count="true" aria-label="Star Oli8/spaper on GitHub">Star</a>
  <a class="github-button" href="https://github.com/sponsors/Oli8" data-color-scheme="no-preference: dark; light: light; dark: dark;" data-icon="octicon-heart" data-size="large" aria-label="Sponsor @Oli8 on GitHub">Sponsor</a>
  <p>
    <img src="https://img.shields.io/npm/v/spaper" alt="version" />
  </p>

  <hr/>

  <section class="sponsors">
    <h3>Sponsors</h3>

    <p class="margin-bottom">
      Thanks to these generous people! ❤️
    </p>

    <table>
      <tr>
        {#each sponsors as { handle: name, avatar, profile } }
          <td>
            <a href={profile} target="_blank">
              <p class="margin-bottom-small">
                <img src={avatar} alt={name} />
              </p>
              {name}
            </a>
          </td>
        {/each}
      </tr>
    </table>
  </section>
</div>

<script lang="ts">
import { onMount } from 'svelte/internal';
import { Button, Badge } from 'spaper';

let sponsors: {
  handle: string;
  avatar: string;
  profile: string;
}[] = []

onMount(async () => {
  const response = await fetch('https://ghs.vercel.app/sponsors/Oli8');
  sponsors = (await response.json()).sponsors;
})
</script>

<style lang="scss">
div {
  text-align: center;
  img {
    display: inline;
    border: none;
  }
}
.sponsors {
  a { background-image: none; }
  td { border-top: none; }
