<Header name="Start"
        legend="How to install and use Spaper" />

<h4>Import papercss</h4>

Add a link to the CDN into your HTML file:
<Prism language="html"
       source='<link rel="stylesheet" href="https://unpkg.com/papercss/dist/paper.min.css">' />

Or use NPM:

<Prism class="border padding-small"
       source="npm i papercss" />

<Prism class="border padding-small"
       language="html"
       source={cssImportCode} />
<h4>Install spaper</h4>

<Prism class="border padding-small"
       source="npm i spaper" />
<h4>Usage</h4>

<Prism class="border padding-small"
       language="html"
       source={usageCode} />

<script lang="ts">
import Header from '../docs/Header.svelte';
import Prism from 'svelte-prism';
import 'prismjs/themes/prism-okaidia.min.css'

const cssImportCode: string = `<script>
  import 'papercss/dist/paper.min.css'
<\/script>`;

const usageCode: string = `<script>
  import { Button } from 'spaper'
  // or
  import Alert from 'spaper/components/Alert.svelte'
<\/script>

<Alert>...</Alert>
<Button>Click me</Button>`;
</script>
