<Navbar class="padding-small">
  <h1 slot="brand" class="spaper-brand">
    <a href="#">Spaper</a>
  </h1>
  <ul class="inline">
    <li><a href="/start" use:link>Installation</a></li>
    <li>
      <Button isLink external outline="secondary"
              href="https://github.com/Oli8/spaper">
        Github
      </Button>
    </li>
    <li>
        <Button isLink external outline="warning"
                href="https://www.getpapercss.com">
          PaperCSS
        </Button>
    </li>
    <li>
      <form on:submit|preventDefault={searchHandler}>
        <input bind:value={searchValue} on:change={searchHandler}
              type="search" placeholder="Search" list="search" />
        <datalist id="search">
          {#each components as component}
            <option value={component}>
          {/each}
        </datalist>
      </form>
    </li>
  </ul>
</Navbar>

<div class="row margin">
  <aside class="sm-12 md-2 padding paper margin-right-large">
    <div>
      <h4 class="text-secondary margin-bottom-small">Installation</h4>
      <ul>
        <li><a href="/start" use:link use:active>Start</a></li>
      </ul>

      <h4 class="text-secondary margin-bottom-small">Components</h4>
      <ul>
        <ListLinks items={topComponents}
                   {updated} {newComponents} />
        <ul>
          <ListLinks items={formComponents}
                     {updated} {newComponents} />
        </ul>
        <ListLinks items={bottomComponents}
                   {updated} {newComponents} />
      </ul>

      <h4 class="text-secondary margin-bottom-small">PaperCSS</h4>
      <ul>
        <li>
          <a href="https://www.getpapercss.com/docs/utilities/" target="_blank">
            Utilities
          </a>
        </li>
        <li>
          <a href="https://www.getpapercss.com/docs/layout/" target="_blank">
            Layout
          </a>
        </li>
        <li>
          <a href="https://www.getpapercss.com/docs/utilities/dark-mode/" target="_blank">
            Dark Mode
          </a>
        </li>
      </ul>
    </div>
  </aside>

  <main class="sm-12 md-9 padding-large margin-left-large paper">
    <Router {routes} />
  </main>
</div>

<ToTop />

<script lang="ts">
import { Navbar, Button } from 'spaper';
import Router, { link, push } from 'svelte-spa-router';
import active from 'svelte-spa-router/active';
import routes from './routes';
import ListLinks from './docs/ListLinks.svelte';
import ToTop from './docs/ToTop.svelte';

const topComponents = [
  'Alert',
  'Article',
  'Badge',
  'Breadcrumb',
  'Button',
  'Card',
  'Carousel',
  'CloseButton',
  'Collapsible',
  'CopyButton',
  'Form',
];

const formComponents = [
  'Checkbox',
  'Input',
  'Radio',
  'Select',
  'Slider',
  'Switch',
];

const bottomComponents = [
  'Hero',
  'Modal',
  'Navbar',
  'Pagination',
  'Popover',
  'Progress',
  'Skeleton',
  'Table',
  'Tabs',
  'Toast',
];

const updated = [
  'Tabs',
];

const newComponents = [
  'Hero',
];

const components = [
  ...topComponents,
  ...formComponents,
  ...bottomComponents,
];

let searchValue = ''

function searchHandler() {
  if (components.includes(searchValue)) {
      push(`/components/${searchValue}`)

    searchValue = ''
  }
}
</script>

<style lang="scss">
h1 {
  font-size: 2rem;
}
:global {
  aside {
    > div {
      position: sticky;
      top: 0;
    }
    li a {
      background-image: none;
      color: var(--primary);
      &:hover {
        color: var(--secondary);
      }
    }
  }
}
:global(a.active) {
  color: var(--secondary) !important;
}
</style>
