<Header name="CloseButton"
        legend="A button to close stuff" />

<section>
  <Example title="usage" code={exampleCode.usage}
           codeWidth={8}>
  {#if show}
    <div class="margin padding border border-5 border-primary"
        out:fade>
      <CloseButton on:click={() => show = false} />
      Hey there
    </div>
  {/if}
  </Example>
</section>

<section>
  <API {props} componentSource="CloseButton" />
</section>

<script lang="ts">
import { fade } from 'svelte/transition';
import { CloseButton } from 'spaper';
import Example from '../../docs/Example.svelte';
import API, { propDoc } from '../../docs/API.svelte';
import Header from '../../docs/Header.svelte';

