  let show = true;
<\/script>

{#if show}
  <div class="margin padding border border-5 border-primary"
        out:fade>
    <CloseButton on:click={() => show = false} />
    Hey there
  </div>
{/if}`,
};

const props = [
  {
    name: 'ariaLabel',
    description: 'Accessibility label',
    type: propDoc.stringType,
    values: propDoc.blank,
    default: 'Close',
  },
];
