<Header name="Toast"
        legend="Display a notification message" />

<section>
  <Example title="usage" code={exampleCode.usage}
           codeWidth={8}>
    <Form>
      <Input label="message" bind:value={message} />
      <Input label="duration (seconds)" type="number"
             bind:value={duration} />
      <Select bind:value={toastType} label="type">
        {#each types as t}
          <option value={t}>{t}</option>
        {/each}
      </Select>
      <Select bind:value={toastPosition} label="position">
        {#each positions as p}
          <option value={p}>{p}</option>
        {/each}
      </Select>
      <Checkbox bind:checked={pauseOnHover} label="Pause on hover" />
      <Checkbox bind:checked={dismissible} label="Dismissible" />
      <Checkbox bind:checked={indefinite} label="indefinite" />

      <Button on:click={displayToast} nativeType="button" type="secondary">
        go
      </Button>
    </Form>
  </Example>
</section>

<section>
  <Example title="Methods" code={exampleCode.methods}
           codeWidth={8}>
    <Button type="secondary"
            on:click={() => info("Something quite neutral, I don't know")}>
      Show
    </Button>
    <Button type="success"
            on:click={() => success('Transaction confirmed!')}>
      Show
    </Button>
    <Button type="warning"
            on:click={() => warning('Be careful with that')}>
      Show
    </Button>
    <Button type="danger"
            on:click={() => danger('Something went wrong :(')}>
      Show
    </Button>
  </Example>
</section>

<section>
  <h3>Methods</h3>
  <ul>
    <li class="margin-bottom-small">
      <code>
        open(message|config)
      </code>
    </li>
    <li class="margin-bottom-small">
      <code>
        info(message|config)
      </code>
    </li>
    <li class="margin-bottom-small">
      <code>
        success(message|config)
      </code>
    </li>
    <li class="margin-bottom-small">
      <code>
        warning(message|config)
      </code>
    </li>
    <li class="margin-bottom-small">
      <code>
        warn(message|config)
      </code>
      <em>alias for warning</em>
    </li>
    <li class="margin-bottom-small">
      <code>
        danger(message|config)
      </code>
    </li>
    <li class="margin-bottom-small">
      <code>
        error(message|config)
      </code><em>alias for danger</em>
    </li>
  </ul>
</section>

<section>
  <API {props} componentSource="Toast/Toast" />
</section>

<script lang="ts">
import { Form, Input, Button, Select,
         Checkbox, Toast } from 'spaper';
import { info, success, warning, danger } from 'spaper/components/Toast';
import Example from '../../docs/Example.svelte';
import API, { propDoc } from '../../docs/API.svelte';
import Header from '../../docs/Header.svelte';

let message = 'hey'
let duration = 2
const types = [
  'success', 'primary', 'secondary',
  'warning', 'danger'
]
let toastType = 'success'
const positions = [
  'top-right', 'bottom-right',
  'bottom-left', 'top-left',
  'top', 'bottom',
]
let toastPosition = positions[0]
let pauseOnHover = false
let dismissible = false
let indefinite = false

function displayToast() {
  Toast.open({
    message,
    type: toastType,
    duration: +duration * 1000,
    position: toastPosition,
    pauseOnHover,
    dismissible,
    indefinite,
  })
}


const exampleCode: Record<string, string> = {
  usage: `
<script>
const positions = [ ... ]
const types = [ ... ]
let message = 'hey'
let duration = 2
let type = 'success'
let position = 'top-right'
let pauseOnHover = false
let dismissible = false
let indefinite = false

function displayToast() {
  Toast.open({
    message,
    type,
    duration: +duration * 1000,
    position,
    pauseOnHover,
    dismissible,
    indefinite,
  })
}

<Form>
  <Input label="message" bind:value={message} />
  <Input label="duration (seconds)" type="number" bind:value={duration} />
  <Select bind:value={type} label="type">
    {#each types as t}
      <option value={t}>{t}</option>
    {/each}
  </Select>
  <Select bind:value={position} label="position">
    {#each positions as p}
      <option value={p}>{p}</option>
    {/each}
  </Select>
  <Checkbox bind:checked={pauseOnHover} label="Pause on hover" />
  <Checkbox bind:checked={dismissible} label="Dismissible" />
  <Checkbox bind:checked={indefinite} label="indefinite" />

  <Button on:click={displayToast} type="secondary">
    go
  </Button>
</Form>`,
  methods:
`
<script>
import { info, success, warning, danger } from 'spaper/components/Toast';
<\/script>

<Button type="secondary"
        on:click={() => info("Something quite neutral, I don't know")}>
  Show
</Button>
<Button type="success"
        on:click={() => success('Transaction confirmed!')}>
  Show
</Button>
<Button type="warning"
        on:click={() => warning('Be careful with that')}>
  Show
</Button>
<Button type="danger"
        on:click={() => danger('Something went wrong :(')}>
  Show
</Button>
`
};

const props = [
  {
    name: 'message',
    description: 'Notification content',
    type: propDoc.stringType,
    values: propDoc.blank,
    default: '',
  },
  {
    name: 'type',
    description: 'Type of the notification',
    type: propDoc.stringType,
    values: propDoc.paperTypes,
    default: 'primary',
  },
  {
    name: 'duration',
    description: 'Visibility duration in milliseconds',
    type: propDoc.numberType,
    values: propDoc.blank,
    default: '2000',
  },
  {
    name: 'position',
    description: 'Which position the toast will appear',
    type: propDoc.stringType,
    values: 'top | top-right | bottom-right | bottom | bottom-left | top-left',
    default: 'top-right',
  },
  {
    name: 'pauseOnHover',
    description: 'Toast will stay visible when hovered',
    type: propDoc.booleanType,
    values: propDoc.blank,
    default: propDoc.falseStr,
  },
  {
    name: 'dismissible',
    description: 'Closable toast',
    type: propDoc.booleanType,
    values: propDoc.blank,
    default: propDoc.falseStr,
  },
  {
    name: 'indefinite',
    description: 'Toast will stay visible until dismissed',
    type: propDoc.booleanType,
    values: propDoc.blank,
    default: propDoc.falseStr,
  },
  {
    name: 'closeAriaLabel',
    description: 'Accessibility label for the close button',
    type: propDoc.stringType,
    values: propDoc.blank,
    default: 'close',
  },
  {
    name: 'onClose',
    description: 'Trigger when toast closed',
    type: '() => any',
    values: propDoc.blank,
    default: propDoc.blank,
  },
];
</script>
