<Header name="Popover"
        legend="Display brief text when user hover over" />

<section>
  <Example title="usage" code={exampleCode.usage}>
    <Popover label="left"
             position="left">
      left
    </Popover>
    <Popover label="top"
             position="top">
      top
    </Popover>
    <Popover label="right"
             position="right">
      right
    </Popover>
    <Popover label="bottom"
             position="bottom">
      bottom
    </Popover>
  </Example>

  <Example title="colors" code={exampleCode.colors}>
    <Popover label="primary"
             background="primary">
      primary
    </Popover>
    <Popover label="secondary"
             background="secondary">
      secondary
    </Popover>
    <Popover label="success"
             background="success">
      success
    </Popover>
    <Popover label="warning"
             background="warning">
      warning
    </Popover>
    <Popover label="danger"
             background="danger">
      danger
    </Popover>
  </Example>

  <Example title="states" code={exampleCode.states}>
    <Form class="margin-bottom-large">
      <Switch bind:checked={showPopover}>Show</Switch>
    </Form>
    <div style="margin-top: 4rem">
      <Popover label="hey"
               active={showPopover}>
        Toggle me
      </Popover>
      <Popover label="hey"
               always>
        Always showing
      </Popover>
    </div>
  </Example>
</section>

<section>
  <API {props} {slots} componentSource="Popover" />
</section>

<script lang="ts">
import { Popover, Switch, Form } from 'spaper';
import Example from '../../docs/Example.svelte';
import API, { propDoc } from '../../docs/API.svelte';
import Header from '../../docs/Header.svelte';

let showPopover = false;

const exampleCode: Record<string, string> = {
  usage: `
<Popover label="left"
         position="left">
  left
</Popover>
<Popover label="top"
         position="top">
  top
</Popover>
<Popover label="right"
         position="right">
  right
</Popover>
<Popover label="bottom"
         position="bottom">
  bottom
</Popover>`,
  colors: `
<Popover label="primary"
         background="primary">
  primary
</Popover>
<Popover label="secondary"
         background="secondary">
  secondary
</Popover>
<Popover label="success"
         background="success">
  success
</Popover>
<Popover label="warning"
         background="warning">
  warning
</Popover>
<Popover label="danger"
         background="danger">
  danger
</Popover>`,
  states: `
<script>
  let showPopover = false;
<\/script>

<Form>
  <Switch bind:checked={showPopover}>Show</Switch>
</Form>

<Popover label="hey"
         active={showPopover}>
  Toggle me
</Popover>
<Popover label="hey"
         always>
  Always showing
</Popover>`,
};

const props = [
  {
    name: 'label',
    description: 'Popover content',
    type: propDoc.stringType,
    values: propDoc.blank,
    default: propDoc.blank,
  },
  {
    name: 'position',
    description: 'Popover position',
    type: propDoc.stringType,
    values: 'top | right | bottom | left',
    default: 'top',
  },
  {
    name: 'inline',
    description: 'Popover inline display',
    type: propDoc.booleanType,
    values: propDoc.blank,
    default: propDoc.trueStr,
  },
  {
    name: 'background',
    description: 'Popover background',
    type: propDoc.stringType,
    values: propDoc.paperTypes,
    default: 'primary',
  },
  {
    name: 'active',
    description: 'Wether popover is active or not',
    type: propDoc.booleanType,
    values: propDoc.blank,
    default: propDoc.falseStr,
  },
  {
    name: 'always',
    description: 'Popover will always be shown',
    type: propDoc.booleanType,
    values: propDoc.blank,
    default: propDoc.falseStr,
  },
];

const slots = [
  {
    name: 'default',
    description: 'Popover content',
  },
];
</script>
