<Header name="Modal"
        legend="Dialog overlay to include any content" />

<section>
  <Example title="usage" code={exampleCode.usage}>
    <Button on:click={() => showModal = true}>
      Show
    </Button>
    <Modal bind:active={showModal}
           title="Modal Title">
      <p>Hey!</p>
      <p>Here is the modal content</p>
    </Modal>
  </Example>

  <Example title="programmatic" code={exampleCode.programmatic}>
    <Button on:click={showProgrammaticModal}>
      Show
    </Button>
  </Example>
</section>

<section>
  <h3>Methods</h3>
  <ul>
    <li class="margin-bottom-small">
      <code>
        open(props)
      </code>
    </li>
    <li class="margin-bottom-small">
      <code>
        close()
      </code>
    </li>
  </ul>
</section>

<section>
  <API {props} {slots} {events} componentSource="Modal/Modal" />
</section>

<script lang="ts">
import { Modal, Button } from 'spaper';
import Example from '../../docs/Example.svelte';
import API, { propDoc } from '../../docs/API.svelte';
import Header from '../../docs/Header.svelte';

function showProgrammaticModal() {
  const modal = Modal.open({ title: 'Hello' })
  // Close programmatically
  setTimeout(modal.close, 2000)
}

const exampleCode: Record<string, string> = {
  usage: `
<script>
  let showModal = false;
<\/script>

<Button on:click={() => showModal = true}>
  Show
</Button>
<Modal bind:active={showModal}
       title="Modal Title">
  <p>Hey!</p>
  <p>Here is the modal content</p>
</Modal>`,
programmatic: `
<script>
  function showProgrammaticModal() {
    const modal = Modal.open({ title: 'Hello' })
    // Close programmatically
    setTimeout(modal.close, 2000)
  }
<\/script>

<Button on:click={showProgrammaticModal}>
  Show
</Button>`
};

let showModal = false;

const props = [
  {
    name: 'active',
    description: 'Modal state',
    type: propDoc.booleanType,
    values: propDoc.blank,
    default: propDoc.falseStr,
  },
  {
    name: 'title',
    description: 'Modal title',
    type: propDoc.stringType,
    values: propDoc.blank,
    default: propDoc.blank,
  },
  {
    name: 'subtitle',
    description: 'Modal subtitle',
    type: propDoc.stringType,
    values: propDoc.blank,
    default: propDoc.blank,
  },
  {
    name: 'content',
    description: 'Modal content',
    type: propDoc.stringType,
    values: propDoc.blank,
    default: propDoc.blank,
  },
  {
    name: 'component',
    description: 'Component to be injected as the modal content',
    type: 'Svelte Component',
    values: propDoc.blank,
    default: propDoc.blank,
  },
  {
    name: 'closeBtnText',
    description: 'Text of the close button',
    type: propDoc.stringType,
    values: propDoc.blank,
    default: 'Close',
  },
];

const slots = [
  {
    name: 'default',
    description: 'Modal body',
  },
  {
    name: 'footer',
    description: 'Modal footer',
  },
];

const events = [
  {
    name: 'close',
    description: 'Triggers when closed',
    parameters: propDoc.blank,
  },
];
</script>
