<Header name="Collapsible"
        legend="Easy to toggle elements" />

<section>
  <Example title="usage" code={exampleCode.types}>
    <Collapsible label="Did you know?">
      I have a bowling personal best of 194
    </Collapsible>
  </Example>

  <Example title="events" code={exampleCode.events}>
    <Collapsible label="Click me :)"
                 on:open={() => alert('Now you see me')}
                 on:close={() => alert('Bye')}>
      Hey there ^_^
    </Collapsible>
  </Example>
</section>

<section>
  <API {props} {slots} {events} componentSource="Collapsible" />
</section>

<script lang="ts">
import { Collapsible } from 'spaper';
import Example from '../../docs/Example.svelte';
import API, { propDoc } from '../../docs/API.svelte';
import Header from '../../docs/Header.svelte';

const exampleCode: Record<string, string> = {
