    type: propDoc.stringType,
    values: propDoc.blank,
    default: propDoc.blank,
  },
  {
    name: 'open',
    description: 'State of the collapsible',
    type: propDoc.booleanType,
    values: propDoc.blank,
    default: propDoc.falseStr,
  },
];

const slots = [
  {
    name: 'default',
    description: 'Collapsible content',
  },
  {
    name: 'trigger',
    description: 'Collapsible triggering element',
  },
];

const events = [
  {
    name: 'open',
    description: 'Triggers when opened',
    parameters: propDoc.blank,
  },
  {
    name: 'close',
    description: 'Triggers when closed',
    parameters: propDoc.blank,
  },
]
