<table {...$$restProps}
       class:table-alternating={striped}
       class:table-hover={hoverable}>
  {#if showHeader}
    <thead>
      <tr>
        {#each columns as column}
          <th class={headerCLass}>
            {#if typeof column === 'string'}
              {column}
            {:else}
              {column.label ?? column.field}
            {/if}
          </th>
        {/each}
      </tr>
    </thead>
  {/if}
  <tbody>
    {#each data as row}
      <tr>
        {#each getRowFiltered(row) as [columnName, val]}
          <td class={cellCLass}>
            {#if (tmpCol = hasContentFn(getColumnFromName(columnName)))}
              {#if tmpCol.html}
                {@html tmpCol.content(val)}
              {:else}
                {tmpCol.content(val)}
              {/if}
            {:else}
              {#if hasHTMLProp(getColumnFromName(columnName))}
                {@html val}
              {:else}
                {val}
              {/if}
            {/if}
          </td>
        {/each}
      </tr>
    {/each}
  </tbody>
</table>

<script >;
let tmpCol;
export let columns = [];
export let data = [];
export let striped = false;
export let hoverable = false;
export let showHeader = true;
export let headerCLass = '';
export let cellCLass = '';
function getColumnFromName(name) {
    return columns[displayedFields.indexOf(name)];
}
function hasContentFn(col) {
    return isColumnObj(col) && col.content && col;
}
function hasHTMLProp(col) {
    return isColumnObj(col) && col.html && col;
}
function isColumnObj(arg) {
    return arg.field;
}
function getRowFiltered(row) {
    return Object.entries(row).filter(([key, _val]) => displayedFields.includes(key));
}
$: if (!columns.length) {
    columns = Object.keys(data[0] || {});
}
let displayedFields;
$: displayedFields = columns.length ? columns.map((column) => (typeof column === 'string' ? column : column.field)) : (data[0] ? Object.keys(data[0]) : []);
</script>
