<Header name="Switch"
        legend="Switch between two opposing states" />

<section>
  <Example title="usage" code={exampleCode.example}>
    <Form class="margin-bottom-large">
      <Switch>Square</Switch>
      <Switch round>Round</Switch>
      <Switch disabled>Disabled</Switch>
    </Form>
    <Form class="margin-bottom-large">
      <Switch inline>"Inline" Square</Switch>
      <Switch inline round>"Inline" Round</Switch>
    </Form>
  </Example>

  <Example title="binding" code={exampleCode.binding}>
    <Form class="margin-bottom-large">
      <Switch bind:checked={hq}>High Quality</Switch>
    </Form>
    HQ {hq ? 'enabled' : 'disabled'}
  </Example>

  <Example title="tile" code={exampleCode.tile}>
    <Form class="margin-bottom-large">
      <Switch {tile} />
    </Form>
  </Example>
</section>

<section>
  <API {props} {events} componentSource="Form/Switch" />
</section>

<script lang="ts">
import { Switch, Form } from 'spaper';
import Example from '../../docs/Example.svelte';
import API, { propDoc } from '../../docs/API.svelte';
import Header from '../../docs/Header.svelte';

let hq = true;

let tile = {
  on: { label: 'On', background: 'success'},
  off: { label: 'Off', background: 'warning'},
};

const exampleCode: Record<string, string> = {
  example: `
<Form>
  <Switch>Square</Switch>
  <Switch round>Round</Switch>
  <Switch disabled>Disabled</Switch>
</Form>
<Form>
  <Switch inline>"Inline" Square</Switch>
  <Switch inline round>"Inline" Round</Switch>
</Form>`,
  binding: `
<script>
  let hq = false;
<\/script>

<Form>
  <Switch bind:checked={hq}>High Quality</Switch>
</Form>
HQ {hq ? 'enabled' : 'disabled'}`,
  tile: `
<script>
  let tile = {
    on: { label: 'On', background: 'success'},
    off: { label: 'Off', background: 'warning'},
  };
<\/script>

<Form>
  <Switch {tile} />
</Form>
  `,
};

const props = [
  {
    name: 'checked',
    description: 'Switch state',
    type: propDoc.booleanType,
    values: propDoc.blank,
    default: propDoc.falseStr,
  },
  {
    name: 'disabled',
    description: 'Switch disabled state',
    type: propDoc.booleanType,
    values: propDoc.blank,
    default: propDoc.falseStr,
  },
  {
    name: 'round',
    description: 'Round style',
    type: propDoc.booleanType,
    values: propDoc.blank,
    default: propDoc.falseStr,
  },
  {
    name: 'inline',
    description: 'Inline style',
    type: propDoc.booleanType,
    values: propDoc.blank,
    default: propDoc.falseStr,
  },
  {
    name: 'tile',
    description: 'Tile mode',
    type: 'Object',
    values: propDoc.blank,
    default: propDoc.blank,
  },
];

const events = [
  {
    name: 'change',
    description: 'Triggers when switch is toggled',
    parameters: '(enable: boolean)'
  },
]
</script>
