<Header name="Table"
        legend="Display sets of data" />

<section>
  <Example title="basic usage" code={exampleCode.usage}>
    <Table {data} />
  </Example>

  <Example title="columns definition" code={exampleCode.columns}>
    <Table columns={['rank', 'name']} {data} />
    <Table {columns} {data} />
  </Example>

  <Example title="striped" code={exampleCode.striped}>
    <Table {data} striped />
  </Example>

  <Example title="hoverable" code={exampleCode.hoverable}>
    <Table {data} hoverable />
  </Example>

  <Example title="display and html rendering" code={exampleCode.display}>
    <Table data={dataForDisplaySection}
           columns={columsForDisplaySection} />
  </Example>
</section>

<section>
  <API {props} componentSource="Table" />
</section>

<script lang="ts">
import { Table } from 'spaper';
import Example from '../../docs/Example.svelte';
import API, { propDoc } from '../../docs/API.svelte';
import Header from '../../docs/Header.svelte';

const data = [
  { rank: 1, name: 'Novak Djokovic', country: 'Serbia' },
  { rank: 2, name: 'Daniil Medvedev', country: 'Russia' },
  { rank: 3, name: 'Stefanos Tsitsipas', country: 'Greece' },
  { rank: 4, name: 'Alexander Zverev', country: 'Germany' },
];
const columns =  [{field: 'name', label: 'Name'}, {field: 'country', label: 'Country'}];

const dataForDisplaySection = [
  { rank: 1, name: 'Novak Djokovic', countryCode: 'rs' },
  { rank: 2, name: 'Daniil Medvedev', countryCode: 'ru' },
  { rank: 3, name: 'Stefanos Tsitsipas', countryCode: 'gr' },
  { rank: 4, name: 'Alexander Zverev', countryCode: 'de' },
];
const columsForDisplaySection = [
  { field: 'rank', label: 'Rank' },
  { field: 'name', label: 'Name' },
  {
    field: 'countryCode', label: 'Country',
    content: (code) => `<img src="https://flagcdn.com/w40/${code}.png">`,
    html: true,
  },
];

const exampleCode: Record<string, string> = {
  usage: `
<script>
  const data = [
    { rank: 1, name: 'Novak Djokovic', country: 'Serbia' },
    { rank: 2, name: 'Daniil Medvedev', country: 'Russia' },
    { rank: 3, name: 'Stefanos Tsitsipas', country: 'Greece' },
    { rank: 4, name: 'Alexander Zverev', country: 'Germany' },
  ];
<\/script>

<Table {data} />`,
  columns: `
<script>
  const data = [...];
  const columns =  [{field: 'name', label: 'Name'}, {field: 'country', label: 'Country'}];
<\/script>

<Table columns={['rank', 'name']} {data} />
<Table {columns} {data} />`,
  striped: `
<Table {data} striped />`,
  hoverable: `
<Table {data} hoverable />`,
  display: `
<script>
  const data = [
    { rank: 1, name: 'Novak Djokovic', countryCode: 'rs' },
    { rank: 2, name: 'Daniil Medvedev', countryCode: 'ru' },
    { rank: 3, name: 'Stefanos Tsitsipas', countryCode: 'gr' },
    { rank: 4, name: 'Alexander Zverev', countryCode: 'ge' },
  ];
  const columns = [
    { field: 'rank', label: 'Rank' },
    { field: 'name', label: 'Name' },
    {
      field: 'countryCode',
      label: 'Country',
      content: (code) => \`<img src="https://flagcdn.com/w40/\${code}.png">\`,
      html: true,
    },
  ];
<\/script>
<Table {data} {columns} />`,
};

const props = [
  {
    name: 'data',
    description: 'Table data',
    type: propDoc.arrayType,
    values: propDoc.blank,
    default: '[]',
  },
  {
    name: 'columns',
    description: 'Table columns description',
    type: propDoc.arrayType,
    values: propDoc.blank,
    default: '[]',
  },
  {
    name: 'striped',
    description: 'Striped style',
    type: propDoc.booleanType,
    values: propDoc.blank,
    default: propDoc.falseStr,
  },
  {
    name: 'hoverable',
    description: 'Highlight line on hover',
    type: propDoc.booleanType,
    values: propDoc.blank,
    default: propDoc.falseStr,
  },
  {
    name: 'showHeader',
    description: 'Wether to show header or not',
    type: propDoc.booleanType,
    values: propDoc.blank,
    default: propDoc.trueStr,
  },
  {
    name: 'headerCLass',
    description: 'CSS classes to be applied on header',
    type: propDoc.stringType,
    values: propDoc.blank,
    default: propDoc.blank,
  },
  {
    name: 'cellCLass',
    description: 'CSS classes to be applied on cell',
    type: propDoc.stringType,
    values: propDoc.blank,
    default: propDoc.blank,
  },
];
</script>

<style>
:global(table img) {
  border: none;
}
</style>
